import React from "react"
import classes from "./LoginPage.module.css"
import {Field, Form} from "react-final-form";
import FormStateToRedux from "../HOC/FormStateToRedux";
import {composeValidators, required} from "../HOC/FormValidators";
import MakeAsyncFunction from 'react-redux-promise-listener'
import {promiseListener} from "../Redux/redux-store"
import {REGISTER, REGISTER_FAILURE, REGISTER_SUCCESS} from "../Redux/finalFormReducer";
import Preloader from "../common/Preloader";
import InitialErrorsContainer from "../HOC/FormValidators";
import LoopIcon from '@material-ui/icons/Loop';
import {makeStyles} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
    myIcons: {
        color: "white",
        cursor: "pointer"
    }
}))

function LoginPage(props) {
    const styles = useStyles()

    return (
        <div className={classes.modal}>
            {props.isLoading === true ? <Preloader/> : null}
            <h2>Login Page</h2>
            <div className={props.submitError || props.mySubmitErrors ? classes.login_page_error : classes.login_page}>
                <MakeAsyncFunction
                    listener={promiseListener}
                    start={REGISTER}
                    resolve={REGISTER_SUCCESS}
                    reject={REGISTER_FAILURE}
                >
                    {onSubmit => (
                        <Form
                            onSubmit={onSubmit}

                            render={({submitErrors, handleSubmit, form, submitting, pristine}) => {

                                return (
                                    <form onSubmit={handleSubmit}>
                                        <FormStateToRedux form="loginForm"/>
                                        <div className={classes.textarea_wrapper}>
                                            <Field validate={composeValidators(required)} name="Login">
                                                {({input, meta}) => {
                                                    return (
                                                        <>
                                                            <input {...input} name="Login" placeholder="Login"
                                                                   type="text"/>
                                                            {meta.error && meta.touched &&
                                                            <div
                                                                className={classes.spans_wrapper}><span>{meta.error}<span
                                                                className={classes.inner_span}/></span></div>}
                                                        </>
                                                    )
                                                }}
                                            </Field>
                                        </div>
                                        <div className={classes.textarea_wrapper}>
                                            <Field validate={composeValidators(required)} name="Password">
                                                {({input, meta}) => (
                                                    <>
                                                        <input {...input} name="Password" placeholder="Password"
                                                               type="password"/>
                                                        {meta.error && meta.touched &&
                                                        <div className={classes.spans_wrapper}><span>{meta.error}<span
                                                            className={classes.inner_span}/></span></div>}
                                                    </>
                                                )}
                                            </Field>
                                        </div>
                                        <div className={classes.textarea_wrapper}>
                                            <span><Field name="rememberMe" component="input" type="checkbox"/></span>
                                            <label>&nbsp;remember me</label>
                                        </div>
                                        <InitialErrorsContainer/>
                                        {props.captchaURL &&
                                        <div className={classes.captcha_wrapper}>
                                            <img alt={"captcha"} src={props.captchaURL}/>
                                            <span><LoopIcon onClick={props.toChangeCaptcha} className={styles.myIcons}/></span>
                                            <Field validate={composeValidators(required)} name="Captcha">
                                                {({input, meta}) => (
                                                    <>
                                                        <input {...input} name="Captcha" placeholder="Print captcha"
                                                               type="text"/>
                                                        {meta.error && meta.touched &&
                                                        <div
                                                            className={classes.spans_wrapper_captcha}><span>{meta.error}<span
                                                            className={classes.inner_span_captcha}/></span></div>}
                                                    </>
                                                )}
                                            </Field>

                                        </div>
                                        }
                                        <div className={classes.buttons_wrapper}>
                                            <button className={classes.button} type="submit"
                                                    disabled={submitting || pristine}>Submit
                                            </button>
                                            <button className={classes.button} type="button" onClick={form.reset}
                                                    disabled={submitting || pristine}>Reset
                                            </button>
                                        </div>

                                    </form>)
                            }
                            }
                        />
                    )}
                </MakeAsyncFunction>
            </div>
        </div>
    )
}


export default LoginPage