import classes from "./Center.module.css"
import React from "react"
import {Field, Form} from "react-final-form";
import FormStateToRedux from "../HOC/FormStateToRedux";
import {composeValidators, maxValue} from "../HOC/FormValidators";

const Center = (props) => {

    const onSubmit = (values) => {
        props.addMessage(values.TextArea)
        values.TextArea = ""
    }

    return (
        <div className={classes.center_wrapper}>
                <Form onSubmit={onSubmit} render={({handleSubmit, form, submitting, pristine, values}) =>
                    <form onSubmit={handleSubmit}>
                        <FormStateToRedux form="dialogsTextArea" />
                        <div className={classes.textarea_wrapper}>
                            <Field validate={composeValidators(maxValue(30))} name="TextArea">
                                {({input, meta}) => (
                                    <>
                                    <textarea {...input} placeholder="Input your message"/>
                                        {meta.error && meta.touched && <div className={classes.spans_wrapper}><span>{meta.error}<span className={classes.inner_span}/></span></div>}
                                    </>
                                )}
                            </Field>
                        </div>
                        <div>
                            <button className={classes.button} type="submit" disabled={submitting || pristine}>Add</button>
                            <span> </span>
                            <button className={classes.button} type="button" onClick={form.reset} disabled={submitting || pristine}>Clear</button>
                        </div>
                    </form>
                }
                />

            </div>

    )
}

export default Center