import React from "react"
import ReactDOM from "react-dom";
import "./index.css"
import App from "./App";
import store from "./Redux/redux-store";
import {Provider} from "react-redux";
import {HashRouter} from "react-router-dom";

/*setInterval(()=> {
    store.dispatch({type:"FAKE"})
}, 1000)*/

    ReactDOM.render(
/*Компонента Provider это компонента библиотеки react-redux, её можно использовать после установки этой библиотеки
командой 'npm install react-redux --save'. Используется для передачи  store вместо хука Contex*/
        <Provider store={store}>
            <HashRouter>
                <App/>
            </HashRouter>
        </Provider>,
        document.getElementById('root')
    );
