// Actions

const UPDATE_FORM_STATE = 'UPDATE_FORM_STATE'
export const REGISTER = 'REGISTER'
export const REGISTER_SUCCESS =
    'REGISTER_SUCCESS'
export const REGISTER_FAILURE =
    'REGISTER_FAILURE'

// Reducer
export default function finalFormReducer(state = {}, action = {}) {
    switch (action.type) {
        case UPDATE_FORM_STATE:
            return {
                ...state,
                [action.form]: action.payload
            }
        case REGISTER:
            return {
                ...state,
                registering: true
            }
        case REGISTER_SUCCESS:
            return {
                ...state,
                registering: false,
                errors: action.payload
            }

        case REGISTER_FAILURE:
            return {
                ...state,
                registering: false
            }
        default:
            return state
    }
}

// Action Creators
export const updateFormState = (form, state) => {
    return (
        {
            type: UPDATE_FORM_STATE,
            form,
            payload: state
        }
    )
}






