import React from 'react';
import classes from './Dialogs.module.css';
import Left_side from "./left_side"
import Right_side from "./Right_side";
import CenterContainer from "./CenterContainer";

const Dialogs = (props) => {
    let NameElements = props.NameElements.map(
        n =>  <Left_side name={n.name}
                         id={n.id}
                         key={n.id}/>
        );
    let MessageElements = props.MessageElements.map(
        m =>  <Right_side position={m.position}
                          name={m.name}
                          message={m.message}
                          key={m.id}/>);
    return (
        <div className={classes.Dialogs_wrapper}>
            <div className={classes.grid_wrapper}>
                {NameElements}
            </div>
            <div className={classes.grid_wrapper}>
                <CenterContainer />
            </div>
            <div className={classes.grid_wrapper}>
                {MessageElements}
            </div>
        </div>
    )
}
export default Dialogs;