//Редьюсер это чистая функция которая принимает старый State и Action, если нужно этот старый State модифицирует
// по правилам иммутабельности (то есть не меняет старый State, а копирует его в новый State и уже меняет вносит
// изменения в новый State) после чего возвращает новый State или старый State в зависимости были изменения или нет
import {follow_unfollow_API, usersAPI} from "../Api/Api";
import {SetDisabled, SetLoading,} from "./PreloaderReduser";
import {followUserListActionCreator} from "./LeftSideBareReducer";
import {GetPromiseErrorActionCreator} from "./PromiseErrorsReduser";

const FOLLOW = "FOLLOW"

const UNFOLLOW = "UNFOLLOW"

const SETUSERS = "SETUSERS"

const SETACTIVEPAGE = "SETACTIVEPAGE"

const SETTOTALUSERCOUNT = "SETTOTALUSERCOUNT"

const SETSTARTPAGE = "SETSTARTPAGE"

const SETPORTIONOFPAGE = "SETPORTIONOFPAGE"

const SETUSERSONPAGE = "SETUSERSONPAGE"

//Создаем для Редакса инициализационный Стейт для первичной инициализации стейта, т.к. он не может быть undefined
let initialState = {
    FindFriendsData:
        [],
    totalUsersCount: 10,
    usersOnPage: 10,
    activePage: 1,
    startPage: 1,
    portionOfPage: 5
/*    fake: 0*/
}

const FindfriendsReducer = (state = initialState, action) => {
    switch (action.type) {
/*        case "FAKE":
            console.log("case FAKE: " + state.fake)
            return {...state, fake: state.fake + 1}*/
        case FOLLOW: {
            let newState = {
                ...state,
                FindFriendsData: state.FindFriendsData.map(u => {
                    if (u.id === action.userId){
                        return {...u, followed: true}
                    }
                    return u;
                })
            }
            return newState
        }
        case UNFOLLOW: {
            let newState = {
                ...state,
                FindFriendsData: state.FindFriendsData.map(u => {
                    if (u.id === action.userId){
                        return {...u, followed: false}
                    }
                    return u;
                })
            }
            return newState
        }
        case SETUSERS: {
            return {...state, FindFriendsData: action.users}
        }
        case SETACTIVEPAGE: {
            return {...state,
                activePage: action.pageChanged
            }
        }
        case SETTOTALUSERCOUNT: {
            return {...state,
                totalUsersCount: action.count
            }
        }
        case SETSTARTPAGE: {
            return {...state,
                startPage: action.lastPage
            }
        }
        case SETPORTIONOFPAGE: {
            return {...state,
                portionOfPage: action.portionOfPage
            }
        }
        case SETUSERSONPAGE: {
            return {...state,
                usersOnPage: action.usersOnPage
            }
        }
        default:
            return state
    }
}

export const Follow = (userId) => {
    return {
        type: FOLLOW,
        userId
    }
}

export const Unfollow = (userId) => {
    return {
        type: UNFOLLOW,
        userId
    }
}

export const SetUsers = (users) => {
    return {
        type: SETUSERS,
        users
    }
}

export const SetActivePage = (pageChanged) => {
    return {
        type: SETACTIVEPAGE,
        pageChanged
    }
}

export const SetTotalUserCount = (TotalCount) => {
    return {
        type: SETTOTALUSERCOUNT,
        count: TotalCount
    }
}

export const SetStartPage = (lastPage) => {

    return {
        type: SETSTARTPAGE,
        lastPage
    }
}
export const SetPortionOfPage = (portionOfPage) => {
    return {
        type: SETPORTIONOFPAGE,
        portionOfPage
    }
}

export const SetUsersOnPage = (usersOnPage) => {
    return {
        type: SETUSERSONPAGE,
        usersOnPage
    }
}


export const getUsersThunkCreator = (properties1, properties2) => {

    return (dispatch) => {

        dispatch(SetLoading (true))
        const promise = usersAPI.getUsers(properties1, properties2)
            promise.then(data => {
            dispatch(SetUsers(data.items));
            dispatch(SetTotalUserCount(data.totalCount));
            dispatch(followUserListActionCreator(data.items));
            dispatch(SetLoading (false))
        });
        promise.catch(error => {
            dispatch(GetPromiseErrorActionCreator(error.message))
            dispatch(SetLoading(false))
        })

    }
}

export const UnfollowThunkCreator = (id) => {
    return (dispatch) => {
        dispatch(SetDisabled(true, id))
        const promise = follow_unfollow_API.unfollow(id)
            promise.then(data => {
                if (data.resultCode === 0) {
                    dispatch(Unfollow(id))
                }
                dispatch(SetDisabled(false, id))
            });
        promise.catch(error => {
            dispatch(GetPromiseErrorActionCreator(error.message))
        })
    }
}
export const FollowThunkCreator = (id) => {
    return (dispatch) => {
        dispatch(SetDisabled(true, id))
        const promise = follow_unfollow_API.follow(id)
            promise.then(data => {
                if (data.resultCode === 0) {
                    dispatch(Follow(id))
                }
                dispatch(SetDisabled(false, id))
            });
        promise.catch(error => {
            dispatch(GetPromiseErrorActionCreator(error.message))
        })
    }
}
export default FindfriendsReducer
