import React from 'react'
import {connect} from "react-redux";
import Profile from "./Profile";
import {
    getProfileStatusThunkCreator,
    getProfileThunkCreator,
    saveFotoThunkCreator,
    UpdateProfileDescriptionThunkCreator
} from "../Redux/ProfileReducer";
import {withRouter} from "react-router-dom";
import WithAuthHOC from "../HOC/withAuthHOC";
import {compose} from "redux";

class ProfileContainer extends React.Component {

    componentDidMount() {

        let userID = this.props.match.params.userID
        if (!userID) {userID = this.props.isAuthID}
        this.props.getProfileThunkCreator(userID)
        this.props.getProfileStatusThunkCreator(userID)
    }

    render() {
        return (
            <Profile {...this.props} />
        )
    }
}

let mapStateToProps = (state) => {
    return {
        profile: state.ProfilePage.profile,
        isLoading: state.preloader.isLoading,
        isAuthID: state.auth?.id,
        isErrorMessage: state.ProfilePage.addErrorMessage
    }
}
//Compose принимает в первой части лист функций, а во второй аргумент, для которого будут выполняться функции.
// Причем порядок выполнения функций начинается с конца. В итоге получается конвеер когда изначальная функция
// // ProfileContainer пришла и ей сделали вначале WithAuthHOC, потом withRouter, потом connect
const enhance = compose(
    connect(mapStateToProps, {getProfileThunkCreator, getProfileStatusThunkCreator, saveFotoThunkCreator,UpdateProfileDescriptionThunkCreator}),
    withRouter,
    WithAuthHOC
)(ProfileContainer)

//withRouter - это функция которая позволяет добавить данные из адресной строки в виде пропсов, в ту компоненту,
// которую withRouter обрамляет. В данном случае это для считывания хвостика userID в App.js

export default enhance
