//Редьюсер это чистая функция которая принимает старый State и Action, если нужно этот старый State модифицирует
// по правилам иммутабельности (то есть не меняет старый State, а копирует его в новый State и уже меняет вносит
// изменения в новый State) после чего возвращает новый State или старый State в зависимости были изменения или нет
import {profileAPI} from "../Api/Api";
import {SetLoading} from "./PreloaderReduser";
import {GetPromiseErrorActionCreator} from "./PromiseErrorsReduser";

const ADD_POST = "ADD-POST"

const DELETE_POST = "DELETE-POST"

const SET_PROFILE = "SET_PROFILE"

const SET_PROFILE_STATUS = "SET_PROFILE_STATUS"

const ADD_ERROR_MESSAGE = "ADD_ERROR_MESSAGE"

export const AddPostActionCreator = (text) => {
    return {
        type: ADD_POST,
        NewPost: text
    }
}

export const DeletePostActionCreator = (postID) => {
    return {
        type: DELETE_POST,
        postID
    }
}


export const setProfileActionCreator = (profile) => {
    return {
        type: SET_PROFILE,
        profile
    }
}
export const setProfileStatusActionCreator = (status) => {
    return {
        type: SET_PROFILE_STATUS,
        status
    }
}

export const addErrorMessageActionCreator = (errorMessage) => {
    return {
        type: ADD_ERROR_MESSAGE,
        errorMessage
    }
}
//.then .catch это модель перехвата ошибок, если промис вернулся с ошибкой. .then это когда всё хорошо, .catch это логика когда всё плохо
export const getProfileThunkCreator = (userID) => {
    return (dispatch) => {
        dispatch(SetLoading(true))
        const promise = profileAPI.getProfile(userID)
            promise.then(data => {
            dispatch(setProfileActionCreator(data));
            dispatch(SetLoading(false))
        });
        promise.catch(error => {
            dispatch(GetPromiseErrorActionCreator(error.message))
            dispatch(SetLoading(false))
        })
    }
}
//.then .catch это модель перехвата ошибок, если промис вернулся с ошибкой. .then это когда всё хорошо, .catch это логика когда всё плохо
export const getProfileStatusThunkCreator = (userID) => {
    return (dispatch) => {
        dispatch(SetLoading(true))
        const promise = profileAPI.getStatus(userID)
            promise.then(data => {
            dispatch(setProfileStatusActionCreator(data));
            dispatch(SetLoading(false))
        });
        promise.catch(error => {
            dispatch(GetPromiseErrorActionCreator(error.message))
            dispatch(SetLoading(false))
        })
    }
}
//.then .catch это модель перехвата ошибок, если промис вернулся с ошибкой. .then это когда всё хорошо, .catch это логика когда всё плохо
export const getUpdateProfileStatusThunkCreator = (newStatus) => {
    return (dispatch) => {
        dispatch(SetLoading(true))
        const promise = profileAPI.updateStatus(newStatus)
        promise.then(data => {
            if (data.resultCode === 0) {
                dispatch(setProfileStatusActionCreator(newStatus))
            }
            dispatch(SetLoading(false))
        });
        promise.catch(error => {
            dispatch(GetPromiseErrorActionCreator(error.message))
            dispatch(SetLoading(false))
        })
    }
}
//.try .catch это похожая на .then .catch модель перехвата ошибок только используется для async/await,
// если промис вернулся с ошибкой тогда срабатывает .catch и это логика когда всё плохо
export const saveFotoThunkCreator = (foto, userId) => {
    return async (dispatch) => {
        dispatch(SetLoading(true))
        try {
            let response = await profileAPI.uploadFoto(foto)
            if (response.resultCode === 0) {
                dispatch(SetLoading(false))
                dispatch(getProfileThunkCreator(userId))
            }
        } catch (error) {
            dispatch(GetPromiseErrorActionCreator(error.message))
            dispatch(SetLoading(false))
        }

    }
}
//.try .catch это похожая на .then .catch модель перехвата ошибок только используется для async/await,
// если промис вернулся с ошибкой тогда срабатывает .catch и логика когда всё плохо
export const UpdateProfileDescriptionThunkCreator = (data, userId) => {
    return async (dispatch) => {
        dispatch(SetLoading(true))
        try {
            let response = await profileAPI.updateProfileDescription(data)
            if (response.resultCode === 0) {
                dispatch(SetLoading(false))
                dispatch(getProfileThunkCreator(userId))
                dispatch(addErrorMessageActionCreator(undefined))
            } else {
                dispatch(SetLoading(false))
                dispatch(addErrorMessageActionCreator(response.messages))
            }
        } catch (error) {
            dispatch(GetPromiseErrorActionCreator(error.message))
            dispatch(SetLoading(false))
        }

    }
}

//Создаем для Редакса инициализационный Стейт для первичной инициализации стейта, т.к. он не может быть undefined
let initialState = {
    PostsData: [
        {post: "Because it is the planet.", id: 0, likescount: 40},
        {post: "This quetion is too dificult.", id: 1, likescount: 5},
        {post: "Message3", id: 2, likescount: 10},
        {post: "Message4", id: 3, likescount: 50},
        {post: "Message5", id: 4, likescount: 20},
        {post: "Message6", id: 5, likescount: 30}
    ],
    profile: null,
    status: null,
    foto: null,
    addErrorMessage: ""
}

const ProfileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
            let newState = {...state}
            let Post = {
                post: action.NewPost,
                id: newState.PostsData.length + 1,
                likescount: 0
            };
            //вместо метода Push используем запись ниже. Если Post записать вначале, то будет добавлять элемент в
            // массив вначале массива, а не в конце
            newState.PostsData = [...state.PostsData, Post]
            return newState
        }
        case DELETE_POST: {
            return {
                ...state,
                PostsData: state.PostsData.filter(f => f.id !== action.postID)
            }
        }
        case SET_PROFILE: {
            return {
                ...state,
                profile: action.profile
            }
        }
        case SET_PROFILE_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
        case ADD_ERROR_MESSAGE: {
            return {
                ...state,
                addErrorMessage: action.errorMessage
            }
        }
        default:
            return state
    }
}
export default ProfileReducer