import React from 'react';
import {connect} from 'react-redux';
import {Redirect} from "react-router-dom";

function mapStateToProps(state) {
    return {
        isLogged: state.auth.isLogged
    };
}
//HOC - High Order Component это функция которая используется для оборачивания компонты с целью придать компоненте какие-то свойства,
//пропсы или модели поведения. А так же для избежания повторяющегося кода, в нашем случае мы делаем редирект с различных страничек на
// страничку логина, если пользователь не залогинен. Кроме того мы НОС подключаем к стейту чтобы не прокидывать пропсы через все дерево.

const WithAuthHOC = (Component) => {
    let WithAuthRedirect = (props) => {
            if (!props.isLogged) return <Redirect to={"/Login"}/>
            return <Component {...props}/>
    }
    let ConnectedWithAuthRedirect = connect(mapStateToProps)(WithAuthRedirect)
    return ConnectedWithAuthRedirect
}

export default WithAuthHOC