import React from 'react';
import classes from "./Friend.module.css"

const Friend = (props) => {
    return (
        <div className={classes.friends_container}>
            <div className={classes.one_friend_container}>

                    <div className={classes.ava_wrapper}>

                    </div>

                <div className={classes.name_wrapper}>
                    <p>{props.friend}</p>
                </div>

            </div>
        </div>
    )
}
export default Friend;