import React from 'react';
import classes from "./Leftsidebar.module.css";
import {NavLink} from "react-router-dom";
import Friend from "./Friend"


const Leftsidebar = (props) => {
    let FriendsElements = props.state.map(p => <Friend friend={p.name} key={p.id}/>)
    return (
        <div className={classes.Leftsidebar}>
            <ul>
                <li>
                    <NavLink className={classes.Nav_item} activeClassName={classes.active} to="/Profile">
                        Profile
                    </NavLink>
                </li>
                <li>
                    <NavLink className={classes.Nav_item} activeClassName={classes.active} to="/Dialogs">
                        Dialogs
                    </NavLink>
                </li>
                <li>
                    <NavLink className={classes.Nav_item} activeClassName={classes.active} to="/News">
                        News
                    </NavLink>
                </li>
                <li>
                    <NavLink className={classes.Nav_item} activeClassName={classes.active} to="/Music">
                        Music
                    </NavLink>
                </li>
                <li>
                    <NavLink className={classes.Nav_item} activeClassName={classes.active} to="/Settings">
                        Settings
                    </NavLink>
                </li>
                <li className={classes.Find_friends}>
                    <NavLink className={classes.Nav_item} activeClassName={classes.active} to="/Find_friends">
                        Find friends
                    </NavLink>
                </li>
            </ul>
            {/*        <div className={classes.friends_container}><Friend friend={props.state.name}/></div>*/}
            <div className={classes.friends_container}>{FriendsElements}</div>
        </div>
    )
}
export default Leftsidebar;