import React from 'react';
import classes from "./Posts.module.css"
import Post from "./Post/Post.jsx"
import {Field, Form} from "react-final-form";
import FormStateToRedux from "../../HOC/FormStateToRedux";
import {composeValidators, maxValue, minValue} from "../../HOC/FormValidators";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import AcUnitIcon from '@material-ui/icons/AcUnit';
import PromiseErrorsSnackbar from "../../common/PromiseErrorsSnackbar";


const Posts = (props) => {

    let PostsElements = props.posts.map(p => <Post message={p.post} likescount={p.likescount} key={p.id}/>)

    const onSubmit = (values) => {
        props.AddPostActionCreator(values.TextArea)
        values.TextArea = ""
    }
    return (

        <div className={classes.My_posts}>
            <ProfileStatusWithHooks status={props.status}
                           isLoading={props.isLoading}
                           getUpdateProfileStatusThunkCreator={props.getUpdateProfileStatusThunkCreator}
                           idFromProfilePage={props.idFromProfilePage?.userId}
                           isLoggedId={props.isLoggedId}/>

            <Form onSubmit={onSubmit} render={({handleSubmit, form, submitting, pristine}) =>
                <form onSubmit={handleSubmit}>
                    <FormStateToRedux form="postsTextArea"/>
                    <div className={classes.textarea_wrapper}>
                        <Field validate={composeValidators(minValue(5), maxValue(15))} name="TextArea">
                            {({input, meta}) => (
                                <>
                                    <textarea {...input} placeholder="Input your message"
                                              cols="100" rows="5"/>
                                    {meta.error && meta.touched && <div className={classes.spans_wrapper}><span>{meta.error}<span className={classes.inner_span}/></span></div>}
                                </>
                            )}
                        </Field>

                    </div>
                    <div>
                        <button className={classes.button} type="submit" disabled={submitting || pristine}>Add</button>
                        <span> </span>
                        <button className={classes.button} type="button" onClick={form.reset}
                                disabled={submitting || pristine}>Clear
                        </button>
                    </div>
                </form>
            }
            />

            {PostsElements}
            {props.error}
        </div>
    )
}
export default Posts;