import React from 'react';
import classes from './Header.module.css';
import logo from "./adidas.png"
import {NavLink} from "react-router-dom";
import svg from "./sign-out-alt-solid.svg"


const Header = (props) => {
    let events = (id) => {
        props.getProfileThunkCreator(id)
        props.getProfileStatusThunkCreator(id)
    }
    return (
        <header className={classes.Header}>
            <img src={logo} alt={"logo"}/>
            <div className={classes.auth_container}>
                {props.isLogged === true
                    ? <div>
                        <div className={classes.login_wrapper}><NavLink to={"/Profile/" + props.id}> <span onClick={() => events(props.id)}>{props.login}</span></NavLink></div>
                        <div className={classes.logout_wrapper} onClick={props.GetLogOutThunkCreator}>
                        <span className={classes.span_logout}>logout</span>

                           <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                            <path
                                  d="M497 273L329 441c-15 15-41 4.5-41-17v-96H152c-13.3 0-24-10.7-24-24v-96c0-13.3 10.7-24 24-24h136V88c0-21.4 25.9-32 41-17l168 168c9.3 9.4 9.3 24.6 0 34zM192 436v-40c0-6.6-5.4-12-12-12H96c-17.7 0-32-14.3-32-32V160c0-17.7 14.3-32 32-32h84c6.6 0 12-5.4 12-12V76c0-6.6-5.4-12-12-12H96c-53 0-96 43-96 96v192c0 53 43 96 96 96h84c6.6 0 12-5.4 12-12z"></path>
                        </svg>

                        </div>
                      </div>
                    : <NavLink to={"/Login/"}>Login</NavLink>}
            </div>
        </header>
    )
}
export default Header;