import './App.css';
import {Redirect, Route, Switch} from "react-router-dom";
import Leftsidebar from './Leftsidebar/Leftsidebar.jsx';
import Footer from './Footer/Footer.jsx';
import Music from "./Music/Music.jsx";
import Settings from "./Settings/Settings.jsx";
import News from "./News/News.jsx";
import React, {Suspense} from "react";
import DialogsContainer from "./Dialogs/DialogsContainer";
import {connect} from "react-redux";
import ProfileContainer from "./Profile/ProfileContainer";
import HeaderContainer from "./Header/HeaderContainer";
import LoginPageContainer from "./Login/LoginPageContainer";
import {GetInitializeAppThunkCreator} from "./Redux/AppInitializationReducer";
import Preloader from "./common/Preloader";
import WithLazySuspenseHOC from "./HOC/withLazySuspenseHOC";
import PromiseErrorsSnackbar from "./common/PromiseErrorsSnackbar";

//lazy загрузка используется для того, чтобы не все сразу компоненты подтягивались в bundle, чтобы ускорить загрузку основных
//страниц, которые юзер должен увидеть в первую очередь. Когда юзер открывает в первый раз сайт, если нет lazy то сразу весь
// код обрабатывается браузером, а если есть lazy, то только та часть обрабатывается которая не обрамлена в lazy.
// Для этого пишется синтаксис который ниже и компонента оборачивается в тег <Suspense fallback={<div>Загрузка...</div>}></Suspense>
// При оборачивании в Suspense тега Route нужно внимательно смотреть чтобы не было конструкции типа:
// <Suspense fallback={<div>Загрузка...</div>}><Route>...</Route></Suspense>
// <Suspense fallback={<div>Загрузка...</div>}><Route>...</Route></Suspense>
// так ВТОРОЙ Route НЕ РАБОТАЕТ!!!! Нужно использовать конструкцию такого типа:
// <Suspense fallback={<div>Загрузка...</div>}><Route>...</Route><Route>...</Route></Suspense>
// Ну или оборачивать каждую компоненту внутри каждого Route
const FindfriendsContainer = React.lazy(() => import('./Find friends/FindfriendsContainer'));



class App extends React.Component {
    componentDidMount() {
        this.props.GetInitializeAppThunkCreator()
    }

    render() {
        let mapStateToProps = (state) => {
            return {
                state: state.SideBar.FriendsData
            }
        }
        const LeftsidebarContainer = connect(mapStateToProps)(Leftsidebar)

        if (!this.props.isInitializationSuccess) {
            return <Preloader/>
        }

        return (
            <div className="App_wrapper">
                <HeaderContainer/>
                <LeftsidebarContainer/>
                <div className="App_wrapper_content">
                    {/*                    компонента Route следит за адресной строкой в браузере и когда видит "свой" путь рендерит
JSX разметку. При этом для Route абсолютно всё равно как меняется адресная строка, даже если руками пользователь
прописал адрес.*/}
                    {/*хвостик в "/Profile/:userID?" в виде /:userID? нужен для того чтобы записать в нашу компоненту, которую обрамляет НОС withRouter, параметр ID
для загрузки конкретного пользователя по его ID. Знак ? в конце обозначает что этот параметр опциональный, то есть его может не быть, и
если его нет, то Route должен отрендерить просто /Profile с дальнейшей логикой в ней*/}
                    <Switch>
                        <Route path="/Profile/:userID?"
                               render={() => <ProfileContainer/>}/>
                        <Route path="/Dialogs"
                               render={() => <DialogsContainer/>}/>
                        <Route path="/Settings"
                               render={Settings}/>
                        <Route path="/Music"
                               render={Music}/>
                        <Route exact path="/News"
                               render={News}/>

                        <Route path="/Find_friends"
                               render={WithLazySuspenseHOC(FindfriendsContainer)}/>

                        <Route path="/Login/"
                               render={() => <Suspense
                                   fallback={<Preloader/>}><LoginPageContainer/></Suspense>}/>

{/*                        <Route exact path="/"
                               render={() => <ProfileContainer/>}/>*/}

{/*данный способ, указав  <Redirect from="/" to="/profile" />, используется для того, чтобы установить стартовую страницу приложения.
Тоесть если пользователь каким-то образом оказался в корневом урле сайта, то редирект его принудительно перекинет с него на другой урл
и соответственно Роут потом потгрузит нужную компоненту*/}
                        <Redirect from="/" to="/profile" />

                    </Switch>
                    <PromiseErrorsSnackbar/>
                </div>
                <Footer/>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        isInitializationSuccess: state.app.isInitializationSuccess
    }
}

export default connect(mapStateToProps, {GetInitializeAppThunkCreator})(App)
