//Редьюсер это чистая функция которая принимает старый State и Action, если нужно этот старый State модифицирует
// по правилам иммутабельности (то есть не меняет старый State, а копирует его в новый State и уже меняет вносит
// изменения в новый State) после чего возвращает новый State или старый State в зависимости были изменения или нет
const ADD_MESSAGE = "ADD-MESSAGE"

export const AddNewMessageActionCreator = (text) => {
    return {
        type: ADD_MESSAGE,
        NewMessage: text
    }
}

//Создаем для Редакса инициализационный Стейт для первичной инициализации стейта, т.к. он не может быть undefined
let initialState = {
    NameData: [
        {name: "Petr", id: 1},
        {name: "name2", id: 2},
        {name: "name3", id: 3},
        {name: "name4", id: 4},
        {name: "name5", id: 5},
        {name: "name6", id: 6}
    ],
    MessageData: [
        {message: "message1", id: 1, position: 1, name: "name1"},
        {message: "message2", id: 2, position: 2, name: "name2"},
        {message: "message3", id: 3, position: 1, name: "name1"},
        {message: "message4", id: 4, position: 2, name: "name2"},
    ],
}

const DialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_MESSAGE: {
            var end = state.MessageData[state.MessageData.length - 1]
            if (end.position === 1) {
                let Message = {
                    id: end.id + 1,
                    message: action.NewMessage,
                    position: 2,
                    name: "name1"
                };
                let newState = {...state}
                newState.MessageData = [...state.MessageData, Message]
                newState.updateMessageText = ""
                return newState
            } else {
                let Message = {
                    id: end.id + 1,
                    message: action.NewMessage,
                    position: 1,
                    name: "name1"
                };
                let newState = {...state}
                newState.MessageData = [...state.MessageData, Message]
                newState.updateMessageText = ""
                return newState
            }
        }
        default:
            return state
    }
}

export default DialogsReducer