import React from 'react';
import classes from "./Post.module.css"

const Post = React.memo(props => {
	return (
        <div className={classes.post1}>
          <img src="https://i.pinimg.com/originals/39/69/5e/39695e35b889a3516dc7c33aa66d1df5.png" alt=""/>
          <p>{props.message}</p>
          <div>
            <span>Like</span>
            <span> </span>               
            <span>{props.likescount}</span>
          </div>
        </div>        
		)
})
export default Post;