import {GetAuthThunkCreator} from "./authReduser";
import {GetPromiseErrorActionCreator} from "./PromiseErrorsReduser";
import {SetLoading} from "./PreloaderReduser";

const INITIALIZATION_SUCCESS = "INITIALIZATION_SUCCESS"


const AppInitializationActionCreator = () => {
    return {
        type: INITIALIZATION_SUCCESS
    }
}

let initialState = {
    isInitializationSuccess: false
}

const AppInitializationReducer = (state = initialState, action) => {
    switch (action.type) {

        case INITIALIZATION_SUCCESS: {
            return {
                ...state,
                isInitializationSuccess: true,
            }
        }
        default:
            return state
    }
}

export const GetInitializeAppThunkCreator = () => {
    return (dispatch) => {
        let promise = dispatch(GetAuthThunkCreator())
        //dispatch(somethingelse())
        //dispatch(somethingelse())
        Promise.all([promise])
            .then(() => {
            dispatch(AppInitializationActionCreator())
        })
            .catch(error => {
                dispatch(GetPromiseErrorActionCreator(error.message))
                dispatch(SetLoading(false))
            })
    }}

export default AppInitializationReducer

