import {applyMiddleware, combineReducers, createStore} from "redux";
import ProfileReducer from "./ProfileReducer";
import DialogsReducer from "./DialogsReducer";
import LeftSideBarReducer from "./LeftSideBareReducer";
import FindfriendsReducer from "./FindfriendsReducer";
import AuthReducer from "./authReduser";
import PreloaderReducer from "./PreloaderReduser";
import thunkMiddleware from "redux-thunk"
import {composeWithDevTools} from "redux-devtools-extension";
import finalFormReducer from './finalFormReducer'
import createReduxPromiseListener from 'redux-promise-listener'
import asyncSubmissionMiddleware from "../HOC/asyncSubmissionMiddleware";
import AppInitializationReducer from "./AppInitializationReducer";
import PromiseErrorsReducer from "./PromiseErrorsReduser";



// 1.Подключаем редьюсеры через комбайн редьюсеров
let reducers = combineReducers({
    ProfilePage: ProfileReducer,
    MessagePage: DialogsReducer,
    SideBar: LeftSideBarReducer,
    FindfriensPage: FindfriendsReducer,
    auth: AuthReducer,
    preloader: PreloaderReducer,
    finalForm: finalFormReducer,
    app: AppInitializationReducer,
    promiseErrors: PromiseErrorsReducer,


})
// 2.Создаем Cтор через функцию Редакса createStore, и отдаем в него редьюсеры
const reduxPromiseListener = createReduxPromiseListener()
const store = createStore(reducers,
    composeWithDevTools(
    applyMiddleware(thunkMiddleware, reduxPromiseListener.middleware, asyncSubmissionMiddleware),
));

window.store= store

export const promiseListener = reduxPromiseListener

export default store