import React from 'react';
import classes from "./Profile.module.css"
import ProfileInfo from "./ProfileInfo/ProfileInfo";
import PostsContainerWithState from "./Posts/PostsContainer";
import Preloader from "../common/Preloader";

const Profile = (props) => {

    if (props.isLoading === true) {
        return (
            <Preloader/>
        )
    }

    let handleChangeFoto = (event) => {
        props.saveFotoThunkCreator(event.target.files[0], props.profile.userId)
    }

    return (
        <div className={classes.Profile}>
            <div className={classes.top_img}>

            </div>
            <div className={classes.ProfileInfo}>
                <ProfileInfo {...props}/>
                {props.isAuthID === props.profile?.userId
                    ? !props.profile.photos
                        ? <label>
                            Upload file:
                            <input
                                className={classes.input}
                                accept="image/*"
                                type="file"
                                onChange={handleChangeFoto}/>
                        </label>
                        : null
                    : null}

            </div>
            <div className={classes.Posts}>
                <PostsContainerWithState/>
            </div>
        </div>
    )
}
export default Profile;