import React, {useEffect, useState} from 'react';
import classes from "./Status.module.css"
import Preloader from "../../common/Preloader";

const ProfileStatusWithHooks = React.memo(props => {

    const [activeInput, setActiveInput] = useState(false);
    const [newStatus, setNewStatus] = useState(null);

    useEffect(() => {
        setNewStatus(props.status)
    }, [props.status]);

    let handleSelect = (e) => {
        e.target.select();
    };

    let inputAreaActivated = () => {
        setActiveInput(true)
    }

    let spanActivated = () => {
        setActiveInput(false)
        props.getUpdateProfileStatusThunkCreator(newStatus)
    }
    let newStatusFunc = (e) => {
        setNewStatus (e.target.value)
    }

        if (props.isLoading === true) {
            return (
                <Preloader/>
            )
        }
        return (
            <div className={classes.status_container}>
                {activeInput &&
                <div className={classes.input_wrapper}>
                    <input onChange={newStatusFunc} onFocus={handleSelect} autoFocus={true}
                           onBlur={spanActivated} defaultValue={props.status}/>
                </div>}
                {!activeInput &&
                <div className={classes.span_wrapper}>
                    <span onClick={props.idFromProfilePage === props.isLoggedId
                        ? inputAreaActivated
                        : null}>{newStatus}</span>
                </div>}
            </div>
        );
})

export default ProfileStatusWithHooks;