import {connect} from "react-redux";
import LoginPage from "./LoginPage";
import {Redirect} from "react-router-dom";
import React from "react";
import {GetCaptchaURLThunkCreator} from "../Redux/authReduser";

function LoginPageContainer(props) {
    if (props.isLogged === true) {
        return (
            <Redirect to={"/Profile/" + props.isLoggedId}/>
        )
    }
    const toChangeCaptcha = () => {
        props.GetCaptchaURLThunkCreator()
    }
    return <LoginPage {...props} toChangeCaptcha={toChangeCaptcha} />
}

function mapStateToProps(state) {
    return {
        isLogged: state.auth.isLogged,
        isLoggedId: state.auth.id,
        captchaURL: state.auth.captchaURL,
        isLoading: state.preloader.isLoading,
        submitError: state.finalForm.loginForm?.submitErrors,
        mySubmitErrors: state.auth.mySubmitErrors,
    };
}

export default connect(mapStateToProps, {GetCaptchaURLThunkCreator})(LoginPageContainer)


