import {REGISTER, REGISTER_SUCCESS} from "../Redux/finalFormReducer";
import {authAPI, securityAPY} from "../Api/Api";
import {
    GetAuthThunkCreator,
    GetCaptchaURLActionCreator,
    SetMySubmitErrorsActionCreator
} from "../Redux/authReduser";
import {SetLoading} from "../Redux/PreloaderReduser";
import {GetPromiseErrorActionCreator} from "../Redux/PromiseErrorsReduser";


const submit = async (values) => {

    return authAPI.getLogin(values.Login, values.Password, values.rememberMe, values.Captcha)
};

/** This is to mimic the behavior of one of the various Redux async middlewares */
const asyncSubmissionMiddleware = (store) => (next: Next) => (action: Action) => {
    if (action && action.type === REGISTER) {
        store.dispatch(SetLoading(true))
        submit(action.payload).then(
            (data) => {
                if (data.resultCode !== 0) {
                    if (data.resultCode === 10) {
                        const promise = securityAPY.getCaptchaURL()
                            promise.then(data => {
                            store.dispatch(GetCaptchaURLActionCreator(data.url));
                            store.dispatch({type: REGISTER_SUCCESS, payload: data.messages})
                        })
                        promise.catch(error => {
                            store.dispatch(GetPromiseErrorActionCreator(error.message))
                            store.dispatch(SetLoading(false))
                        })
                    }
                    store.dispatch({type: REGISTER_SUCCESS, payload: data.messages})
                    store.dispatch(SetMySubmitErrorsActionCreator(data.messages))
                } else {
                    store.dispatch(GetAuthThunkCreator())
                }
                store.dispatch(SetLoading(false))
            }
        );
    }
    return next(action);
};

export default asyncSubmissionMiddleware;
