import * as axios from "axios";

var Instance = axios.create({
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    withCredentials: true,
    headers: {"API-KEY": "1243c0df-168e-420b-8bca-ea69113860ac"}
});

export const profileAPI = {
    getProfile(userID) {
        return Instance.get(`profile/` + userID)
            .then(response => {
                return response.data
            })
    },
    getStatus(userID) {
        return Instance.get(`profile/status/` + userID)
            .then(response => {
                return response.data
            })
    },
    updateStatus(newStatus) {
        return Instance.put(`profile/status`, {status: newStatus})
            .then(response => {
                return response.data
            })
    },
    uploadFoto(foto) {
        let formData = new FormData();
        formData.append("image:", foto)
        return Instance.put(`profile/photo`, formData)
            .then(response => {
                return response.data
            })
    },
    updateProfileDescription(data) {
        return Instance.put(`profile`, data)
            .then(response => {
                return response.data
            })
    }
}

export const usersAPI = {
    getUsers(properties1, properties2) {
        return Instance.get(`users?page=${properties1}&count=${properties2}`)
            .then(response => {
                return response.data
            })
    }
}
export const securityAPY = {
    getCaptchaURL () {

        return Instance.get(`security/get-captcha-url`)
            .then(response => {
                return response.data
            })
    }
}

export const authAPI = {

    getAuth() {
        return Instance.get(`auth/me`)
            .then(response => {
                return response.data
            })
    },
    getLogin(email, password, rememberMe, captcha) {
        return Instance.post('auth/login', {email, password, rememberMe, captcha})
            .then(responce => {
                return responce.data
            })
    },
    logOut() {
        return Instance.delete('auth/login')
            .then(response => {
                return response.data
            })
    }
}

export const follow_unfollow_API = {
    follow(id) {
        return Instance.post(`follow/${id}`)
            .then(response => {
                return response.data
            })
    },
    unfollow(id) {
        return Instance.delete(`follow/${id}`)
            .then(response => {
                return response.data
            })
    }
}