import React from 'react';
import classes from './left_side.module.css';
import {NavLink} from "react-router-dom";
import ava from "./ava.png"

const left_side = (props) => {
    return (

            <div className={classes.ava_name_wrapper}>

                    <NavLink activeClassName={classes.active} to={"/dialogs/"+props.id}>
                        <div className={classes.ava_wrapper}><img src={ava} alt={"ava"}/></div>
                        <div>{props.name}</div>
                    </NavLink>

            </div>
    )
}

export default left_side;