//Редьюсер это чистая функция которая принимает старый State и Action, если нужно этот старый State модифицирует
// по правилам иммутабельности (то есть не меняет старый State, а копирует его в новый State и уже меняет вносит
// изменения в новый State) после чего возвращает новый State или старый State в зависимости были изменения или нет
const FOLLOWUSERLIST = "FOLLOWUSERLIST"

const FOLLOWUSER = "FOLLOWUSER"

const UNFOLLOWUSER = "UNFOLLOWUSER"

export const followUserListActionCreator = (users) => {
    return {
        type: FOLLOWUSERLIST,
        users
    }
}

export const followUserActionCreator = (user) => {
    return {
        type: FOLLOWUSER,
        user
    }
}
export const unFollowUserActionCreator = (user) => {
    return {
        type: UNFOLLOWUSER,
        user
    }
}

export const setFollowedUsersThunkCreator = (user) => {
    return (dispach) => {
        dispach(followUserActionCreator(user))
    }
}
export const setUnFollowedUsersThunkCreator = (user) => {
    return (dispach) => {
        dispach(unFollowUserActionCreator(user))
    }
}

//Создаем для Редакса инициализационный Стейт для первичной инициализации стейта, т.к. он не может быть undefined
let initialState = {
    FriendsData: []
}

const LeftSideBarReducer = (state = initialState, action) => {
    switch (action.type) {

        case FOLLOWUSER:
            return {
                ...state,
                FriendsData: [...state.FriendsData, action.user]
            }
        case UNFOLLOWUSER:
            return {
                ...state,
                FriendsData: state.FriendsData.filter(friend => friend.id !== action.user)
            }
        case FOLLOWUSERLIST:
            return {
                ...state,
                FriendsData: action.users.filter(u => u.followed)
            }
        default:
            return state
    }

}
export default LeftSideBarReducer