import React, {useEffect} from 'react';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import {makeStyles} from '@material-ui/core/styles';
import {connect} from "react-redux";
import {GetPromiseErrorActionCreator} from "../Redux/PromiseErrorsReduser";


function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
    root: {
        textAlign: "center"
    },
}));

const PromiseErrorsSnackbar = (props) => {
    const styles = useStyles();
    const [open, setOpen] = React.useState(false);

    useEffect(() => {
        if (props.error) {
            setOpen(true);
        }
    }, [props.error])

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        props.GetPromiseErrorActionCreator(null)
        setOpen(false);
    };

    return (
        <div>
            <Snackbar open={open} onClose={handleClose}>
                <Alert onClose={handleClose} severity="error" className={styles.root}>
                    Something wrong happened. Response from server: <p>{props.error}</p>
                </Alert>
            </Snackbar>
        </div>
    );
}

let mapStateToProps = (state) => {
    return {
        error: state.promiseErrors?.error
    }
}

const PromiseErrorsSnackbarWithState = connect(mapStateToProps, {GetPromiseErrorActionCreator})(PromiseErrorsSnackbar)

export default PromiseErrorsSnackbarWithState
