import React from 'react';
import classes from "./ProfileInfo.module.css"
import img from './1.png'
import Preloader from "../../common/Preloader";
import ProfileDescription from "./ProfileDescription";
import EditWithAPIin from "./EditWithAPIin";

const ProfileInfo = (props) => {

    const [editMode, setEditMode] = React.useState(false)

    if (!props.profile) {
        return null
    }
    if (props.isLoading === true) {
        return (
            <Preloader/>
        )
    }
    let onSelectInput = () => {
        document.getElementById('file-input').click()
    }

    let onChangeFoto = (event) => {
        props.saveFotoThunkCreator(event.target.files[0], props.profile.userId)
    }

    let onChangeProfileDescription = () => {
        setEditMode(true)
    }

    let onSubmitProfileDescription =(data) => {
        setEditMode(false)
        props.UpdateProfileDescriptionThunkCreator (data, props.profile.userId)
    }

    return (
        <div className={classes.ProfileInfo}>
            <div className={classes.avatar_img}>
                <label>
                    <input
                        className={classes.input}
                        accept="image/*"
                        type="file"
                        id="file-input"
                        style={{display: "none"}}
                        onChange={onChangeFoto}
                    />
                </label>
                <img onClick={onSelectInput} src={!props.profile.photos.large ? null : props.profile.photos.large}
                     alt={!props.profile.photos.large ? props.profile.fullName + " didn't add foto" : props.profile.fullName}/>
            </div>
            {editMode || props.isErrorMessage
                ? <EditWithAPIin {...props} onSubmitProfileDescription={onSubmitProfileDescription} />
                : <div><ProfileDescription {...props}/> <button onClick={onChangeProfileDescription} className={classes.editModeButton}>Edit data</button></div>
            }
{/*            <EditWithAPIin {...props} onSubmitProfileDescription={onSubmitProfileDescription} />*/}

        </div>
    )
}
export default ProfileInfo;