import React from "react";
import classes from "./ProfileInfo.module.css";
import * as yup from 'yup';
import {Field, Formik, useField} from 'formik';
import {isEqual} from 'lodash';
import {DisplayFormikState} from "./DisplayFormikState";
import {AxiosInstance as axios} from "axios";

const EditWithAPIin = (props) => {


    const MyTextField = ({label, ...props}) => {
        const [field, meta] = useField(props);
        if (field.checked === undefined) {
            return (<>
                    <div className={classes.rowWrapper}>
                        <label>{label}</label>
                        <input {...field} {...props} />

                    </div>
                    {meta.error ? (
                        <div className={classes.input_feedback}>{meta.error}</div>
                    ) : null}
                </>
            );
        } else {
            return (
                <div className={classes.fieldWithCheckboxWrapper}>
                    <label>{label}</label>
                    <span><input {...field} {...props} />
                    <span>&nbsp;{`${field.checked}`}</span></span>
                    {meta.touched && meta.error ? (
                        <div className={classes.input_feedback}>{meta.error}</div>
                    ) : null}
                </div>
            );
        }
    };

    let obj = {
        contacts: {}
    };
    const FindWord = (countOfArray, arrayWithErrors, target) => {
        let i = 0;
        while (i < countOfArray) {
            if (arrayWithErrors[i].indexOf(target) > 0) {
                return arrayWithErrors[i]
            }
            i++;
        }
    }
    if (props.isErrorMessage) {



        let arrayWithErrors = props.isErrorMessage
        let countOfArray = arrayWithErrors.length
        let target = {
            "fullName": 'FullName',
            "aboutMe": 'AboutMe',
            "lookingForAJobDescription": "LookingForAJobDescription",
            "facebook": 'Facebook',
            "website": 'Website',
            "vk": 'Vk',
            "twitter": 'Twitter',
            "instagram": 'Instagram',
            "youtube": 'Youtube',
            "github": 'Github',
            "mainLink": 'MainLink',
        }; // цель поиска
        let arrayOfErrors = []
        let arrayNameForms = []
        for (let key in target) {
            let nameOfError = FindWord(countOfArray, arrayWithErrors, target[key])
            if (nameOfError !== undefined) {
                arrayOfErrors.push(nameOfError)
                arrayNameForms.push(key)
            }
        }

        for (let i = 0; i < arrayOfErrors.length; i++) {
            let x = arrayNameForms[i];
            if (x === "facebook" || x === "website" || x === "vk" || x === "twitter" || x === "instagram" || x === "youtube" || x === "github" || x === "mainLink") {
                let key = arrayNameForms[i];
                obj.contacts[key] = arrayOfErrors[i];
            } else {
                let key = arrayNameForms[i];
                obj[key] = arrayOfErrors[i];
            }
        }
    }

    return <>
{/*                <div>
            <Formik
                initialValues={{
                    fullName: props.profile.fullName,
                    aboutMe: props.profile.aboutMe,
                    lookingForAJob: props.profile.lookingForAJob,
                    lookingForAJobDescription: props.profile.lookingForAJobDescription,
                    contacts: {
                        facebook: props.profile.contacts.facebook ? props.profile.contacts.facebook : "",
                        website: props.profile.contacts.website ? props.profile.contacts.website : "",
                        vk: props.profile.contacts.vk ? props.profile.contacts.vk : "",
                        twitter: props.profile.contacts.twitter ? props.profile.contacts.twitter : "",
                        instagram: props.profile.contacts.instagram ? props.profile.contacts.instagram : "",
                        youtube: props.profile.contacts.youtube ? props.profile.contacts.youtube : "",
                        github: props.profile.contacts.github ? props.profile.contacts.github : "",
                        mainLink: props.profile.contacts.mainLink ? props.profile.contacts.mainLink : "",
                    }
                }}
                validationSchema={yup.object().shape({
                    fullName: yup.string()
                        .required('Name is required!'),
                    aboutMe: yup.string()
                        .required('Field cannot be empty!'),
                    lookingForAJobDescription: yup.string()
                        .required('Field cannot be empty!'),
                })}
                onSubmit={async (values, actions) => {
                    try {
                        const response = await axios.put(
                            'https://social-network.samuraijs.com/api/1.0/profile',
                            values,
                            {
                                withCredentials: true,
                                headers: {"API-KEY": "1243c0df-168e-420b-8bca-ea69113860ac"}
                            }
                        )

                        if (response.data.resultCode === 0) {
                            props.onSubmitProfileDescription(values)
                        }
                        if (response.data.resultCode === 1) {
                            actions.setStatus(response.data.messages)

                            let arrayWithErrors = response.data.messages
                            let countOfArray = arrayWithErrors.length
                            let target = {
                                "fullName": 'FullName',
                                "contacts.facebook": 'Facebook',
                                "contacts.website": 'Website',
                                "contacts.vk": 'Vk',
                                "contacts.twitter": 'Twitter',
                                "contacts.instagram": 'Instagram',
                                "contacts.youtube": 'Youtube',
                                "contacts.github": 'Github',
                                "contacts.mainLink": 'MainLink',
                            }; // цель поиска
                            for (let key in target) {
                                let nameOfError = FindWord(countOfArray, arrayWithErrors, target[key])
                                actions.setFieldError(key, nameOfError)
                            }
                        }

                    } catch (e) {
                        debugger
                    }
                }}
            >
                {props => {
                    const hasChanged = !isEqual(props.values, props.initialValues);
                    const hasErrors = Object.keys(props.errors).length > 0;
                    return (
                        <form onSubmit={props.handleSubmit}>
                            <MyTextField name="fullName" type="text" label="Name" status="props.status"/>
                            <MyTextField name="aboutMe" type="text" label="About Me"/>
                            <MyTextField name="lookingForAJob" type="checkbox" label="looking For A Job:"/>
                            <MyTextField name="lookingForAJobDescription" type="text" label="Description Of Job"/>
                            <div style={{marginBottom: '0.5rem'}}>Contacts:</div>
                            <MyTextField name="contacts.facebook" type="text" label="Facebook:"/>
                            <MyTextField name="contacts.website" type="text" label="Website:" />
                            <MyTextField name="contacts.vk" type="text" label="Vk:" />
                            <MyTextField name="contacts.twitter" type="text" label="Twitter:" />
                            <MyTextField name="contacts.instagram" type="text" label="Instagram:" />
                            <MyTextField name="contacts.youtube" type="text" label="Youtube:" />
                            <MyTextField name="contacts.github" type="text" label="Github:" />
                            <MyTextField name="contacts.mainLink" type="text" label="MainLink:" />
                            <div>{props.status}</div>
                            <button
                                type="button"
                                className={classes.editModeButton}
                                onClick={props.handleReset}
                                disabled={!hasChanged || props.isSubmitting}>
                                Reset
                            </button>
                            <button
                                className={classes.editModeButton}
                                type="submit"
                                disabled={!hasChanged || hasErrors || props.isSubmitting}>
                                Submit
                            </button>
                            <DisplayFormikState {...props} />
                        </form>
                    )
                }}
            </Formik>
        </div>*/}
        <Formik
            enableReinitialize={true}
            initialErrors={{
                ...obj
            }}
            initialValues={{
                fullName: props.profile.fullName,
                aboutMe: props.profile.aboutMe,
                lookingForAJob: props.profile.lookingForAJob,
                lookingForAJobDescription: props.profile.lookingForAJobDescription,
                contacts: {
                    facebook: props.profile.contacts.facebook ? props.profile.contacts.facebook : "",
                    website: props.profile.contacts.website ? props.profile.contacts.website : "",
                    vk: props.profile.contacts.vk ? props.profile.contacts.vk : "",
                    twitter: props.profile.contacts.twitter ? props.profile.contacts.twitter : "",
                    instagram: props.profile.contacts.instagram ? props.profile.contacts.instagram : "",
                    youtube: props.profile.contacts.youtube ? props.profile.contacts.youtube : "",
                    github: props.profile.contacts.github ? props.profile.contacts.github : "",
                    mainLink: props.profile.contacts.mainLink ? props.profile.contacts.mainLink : "",
                }
            }}
            validationSchema={yup.object().shape({
                fullName: yup.string()
                    .required('Name is required!'),
                aboutMe: yup.string()
                    .required('Field cannot be empty!'),
                lookingForAJobDescription: yup.string()
                    .required('Field cannot be empty!'),
            })}
            onSubmit={(values, {setSubmitting}) => {
                props.onSubmitProfileDescription(values)
                setSubmitting(false);
            }}>
            {props => {
                const {values, errors, initialValues, isSubmitting, handleSubmit, handleReset} = props;
                const hasChanged = !isEqual(values, initialValues);
                const hasErrors = Object.keys(errors).length > 0;

                return (
                    <div className={classes.formWrapper}>
                        <form onSubmit={handleSubmit}>
                            <MyTextField name="fullName" type="text" label="Name" status="props.status"/>
                            <MyTextField name="aboutMe" type="text" label="About Me"/>
                            <MyTextField name="lookingForAJob" type="checkbox" label="looking For A Job:"/>
                            <MyTextField name="lookingForAJobDescription" type="text" label="Description Of Job"/>
                            <div style={{marginBottom: '0.5rem'}}>Contacts:</div>
                            <MyTextField name="contacts.facebook" type="text" label="Facebook:"/>
                            <MyTextField name="contacts.website" type="text" label="Website:"/>
                            <MyTextField name="contacts.vk" type="text" label="Vk:"/>
                            <MyTextField name="contacts.twitter" type="text" label="Twitter:"/>
                            <MyTextField name="contacts.instagram" type="text" label="Instagram:"/>
                            <MyTextField name="contacts.youtube" type="text" label="Youtube:"/>
                            <MyTextField name="contacts.github" type="text" label="Github:"/>
                            <MyTextField name="contacts.mainLink" type="text" label="MainLink:"/>
                            <button
                                type="button"
                                className={classes.editModeButton}
                                onClick={handleReset}
                                disabled={!hasChanged || isSubmitting}>
                                Reset
                            </button>
                            <button
                                type="submit"
                                className={classes.editModeButton}
                                disabled={!hasChanged || hasErrors || isSubmitting}>
                                Submit
                            </button>
                            <DisplayFormikState {...props} />
                        </form>
                    </div>
                );
            }
            }
        </Formik>

    </>
}
export default EditWithAPIin