//Редьюсер это чистая функция которая принимает старый State и Action, если нужно этот старый State модифицирует
// по правилам иммутабельности (то есть не меняет старый State, а копирует его в новый State и уже меняет вносит
// изменения в новый State) после чего возвращает новый State или старый State в зависимости были изменения или нет

const SETLOADING = "SETLOADING"

const SETDISABLED = "SETDISABLED"

export const SetLoading = (isLoading) => {
    return {
        type: SETLOADING,
        isLoading
    }
}

export const SetDisabled = (isDisabled, userID) => {
    return {
        type: SETDISABLED,
        isDisabled,
        userID
    }
}
//Создаем для Редакса инициализационный Стейт для первичной инициализации стейта, т.к. он не может быть undefined
let initialState = {
    isLoading: false,
    isProgress: []
}

const PreloaderReducer = (state = initialState, action) => {
    switch (action.type) {

        case SETLOADING: {
            return {...state, isLoading: action.isLoading}
        }

        case SETDISABLED: {
            return {...state,
                isProgress: action.isDisabled
                ? [...state.isProgress, action.userID]
                : state.isProgress.filter(id => id !== action.userID)
            }
        }

        default:
            return state
    }
}
export default PreloaderReducer
