//компоненты типа "Container" создаются для ограничения презентационных компонент от прямого обращения
// к store и могут содержать методы и функции из Redux. При этом компоненты которые используются для отрисовки UI
// в идеале должны быть чистыми
import {AddNewMessageActionCreator} from "../Redux/DialogsReducer";
import Center from "./Center";
import {connect} from "react-redux";


let mapStateToProps = (state) => {
    return {

    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        addMessage: (text) => {
            let action = AddNewMessageActionCreator(text)
            dispatch(action)
        },
    }
}
/*connect - функция из библиотеки React-Redux, которая передает в компоненту props и dispatch. При этом данные
 передаются через вспомогательную функцию mapStateToProps, mapStateToProps работает только со state, а не с store.*/

const CenterContainer = connect (mapStateToProps,mapDispatchToProps)(Center)

export default CenterContainer;

