import {connect} from "react-redux";
import React from "react";
import classes from "../Login/LoginPage.module.css";

const InitialError = (props) => {
    return (
        <div className={classes.initial_error_wrapper}>{props.mySubmitErrors}</div>
    )
}

function mapStateToProps(state) {
    return {
        mySubmitErrors: state.auth.mySubmitErrors,
    };
}

const InitialErrorsContainer = connect(mapStateToProps, undefined)(InitialError)

export default InitialErrorsContainer


export const required = (value) => {
    return (value ? undefined : 'Required')
}

export const maxValue = (max) => {
    return (value) => {
        if (value !== undefined) {
            if (value.length >= max) {
                return `Should be lesser than ${max} symbols`
            } else {
                return null
            }
        }
    }
}

export const minValue = (min) => {
    return (value) => {
        if (value) {
            if (value.length >= min) {
                return null
            } else {
                return `Should be longer than ${min} symbols`
            }
        }
    }
}

export const composeValidators = (...validators) => {
    return (value) =>
        validators.reduce(function(error, validator) {
            return error || validator(value)
        }, undefined)
}