import React from 'react';
import {
    AddPostActionCreator,
    getUpdateProfileStatusThunkCreator,
} from "../../Redux/ProfileReducer"
import Posts from "./Posts";
import {connect} from "react-redux";

class PostsContainer extends React.Component {

    render() {
        return (
            <Posts {...this.props} />
        )
    }
}

let mapStateToProps = (state) => {
    return {
        posts: state.ProfilePage.PostsData,
        status: state.ProfilePage.status,
        isLoading: state.preloader.isLoading,
        isLoggedId: state.auth.id,
        idFromProfilePage: state.ProfilePage.profile,
    }
}

/*connect - функция из библиотеки React-Redux, которая передает в компоненту props и dispatch. При этом данные
 передаются через вспомогательную функцию mapStateToProps, mapStateToProps работает только со state, а не с store.*/

let PostsContainerWithState = connect(mapStateToProps, {
    AddPostActionCreator,
    getUpdateProfileStatusThunkCreator
})(PostsContainer)

export default PostsContainerWithState;