//компоненты типа "Container" создаются для ограничения презентационных компонент от прямого обращения
// к store и могут содержать методы и функции из Redux. При этом компоненты которые используются для отрисовки UI
// в идеале должны быть чистыми
import React from 'react';
import Dialogs from "./Dialogs";
import {connect} from "react-redux";
import WithAuthHOC from "../HOC/withAuthHOC";
import {compose} from "redux";

let mapStateToProps = (state) => {
    return {
        NameElements: state.MessagePage.NameData,
        MessageElements: state.MessagePage.MessageData,
        updateMessageText: state.MessagePage.updateMessageText,
        deleteMessageText: state.MessagePage.deleteMessageText,
    }
}
//Compose принимает в первой части лист функций, а во второй аргумент, для которого будут выполняться функции.
// Причем порядок выполнения функций начинается с конца. В итоге получается конвеер когда изначальная функция
// Dialogs пришла и ей сделали вначале connect, потом WithAuthHOC
const enhance = compose(
    WithAuthHOC,
    connect (mapStateToProps)
)(Dialogs)

/*connect - функция из библиотеки React-Redux, которая передает в компоненту props и dispatch. При этом данные
передаются через вспомогательную функцию mapStateToProps, mapStateToProps работает только со state, а
не с store.*/

export default enhance;
