import React from 'react'
import Header from "./Header";
import {connect} from "react-redux";
import {GetLogOutThunkCreator} from "../Redux/authReduser";
import {getProfileStatusThunkCreator, getProfileThunkCreator} from "../Redux/ProfileReducer";
import {compose} from "redux";

class HeaderContainer extends React.Component {


    render() {
        const {...other} = this.props
        return (
            <Header {...other}/>
        )
    }
}

let mapStateToProps = (state) => {
    return {
        isLogged: state.auth.isLogged,
        isLoading: state.preloader.isLoading,
        id: state.auth.id,
        login: state.auth.login,
        email: state.auth.email,
    }
}


export default compose(
    connect(mapStateToProps, {getProfileThunkCreator, getProfileStatusThunkCreator, GetLogOutThunkCreator},
    )
    (HeaderContainer))