import React from "react"
import classes from "./Right_side.module.css"
import svg from "./back.svg";

const Right_side = (props) => {
    switch (props.position) {
        case 1:
            return (
                <div className={classes.Right_side_container}>
                    <div className={classes.face_chat_wrapper}>

                    </div>

                    <div className={classes.face_chat_wrapper}>
                        <div className={classes.face_grid_wrapper}>
                            <div className={classes.face_wrapper}>

                            </div>
                        </div>
                        <div className={classes.chat_name}>
                            {props.name}
                        </div>
                        <div className={classes.triangle}>
                            <img src={svg} alt={"svg"}/>
                        </div>
                        <div className={classes.chat}>
                            <p>
                                {props.message}
                            </p>
                        </div>
                    </div>

                </div>
            )
        default:
            return (
                <div className={classes.Right_side_container}>
                    <div className={classes.face_chat_wrapper}>
                        <div className={classes.face_grid_wrapper}>
                            <div className={classes.face_wrapper}>

                            </div>
                        </div>
                        <div className={classes.chat_name}>
                            {props.name}
                        </div>
                        <div className={classes.triangle}>
                            <img src={svg} alt={"svg"}/>
                        </div>
                        <div className={classes.chat}>
                            <p>
                                {props.message}
                            </p>
                        </div>
                    </div>

                    <div className={classes.face_chat_wrapper}>

                    </div>

                </div>
            )
    }
}

export default Right_side