import React from "react";
import classes from "./ProfileInfo.module.css";

const ProfileDescription = (props) => {

    return (
        <div className={classes.ProfileDescription}>
            <div className={classes.Name}>
                {props.profile.fullName}
            </div>
            <div className={classes.About}>
                About:&nbsp;{!props.profile.aboutMe ? "none" : props.profile.aboutMe}
            </div>
            <div className={classes.lookingForAJob}>
                looking For A Job:&nbsp;{!props.profile.lookingForAJob ? "none" : "YES"}
            </div>
            <div className={classes.lookingForAJobDescription}>
                Description Of
                Job:&nbsp;{!props.profile.lookingForAJobDescription ? "none" : props.profile.lookingForAJobDescription}
            </div>
            <div className={classes.Web}>
                {Object.entries(props.profile.contacts).map(([key, value]) =>
                    <div key={key}>
                        <b>{key}:&nbsp;</b><i>{!value ? "none" : value}</i>
                    </div>
                )}
            </div>
        </div>
    )
}

export default ProfileDescription